@import './hud_print.css';
.hud-search-container {
  background: var(--gray-5);
}

.hud-search-container__text {
  padding: 0.9375em;
}

#hud-hca-api-map-canvas {
  height: 320px;
}

#hud-hca-api-query {
  max-width: 30em;
}

@media only all and (min-width: 56.3125em) {
  .hud-search-container {
    display: flex;
  }
  .hud-search-container__text {
    flex: 1;
    min-width: 16.875em;
    padding: 1.875em;
  }
  .hud-search-container__map {
    flex: 2;
  }
  #hud-hca-api-map-canvas {
    height: 440px;
  }
}
#hud-hca-api-map-container {
  /* Prevent map controls from appearing on top of mega menu. */
  z-index: 0;
}

#hud-hca-api-map-canvas img {
  max-width: none;
}

@media only all and (min-width: 37.5em) {
  .hud-hca-api-results-actions {
    margin-left: 20px;
    float: right;
  }
}
.hud-hca-api-results-actions abbr, .hud-hca-api-results-actions svg {
  border-bottom: none;
  text-decoration: none;
}

.o-table tr {
  vertical-align: top;
}

.no-js.o-featured-content-module {
  min-height: auto;
}
.no-js.o-featured-content-module .o-featured-content-module__text {
  width: auto;
}
.no-js .hud-search-container__map,
.no-js #hud_print-page-link {
  display: none;
}